
body{
    font-size: 16px;
}



.main_div{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0; /* Set a background color for the entire container */

}

.card {
    height: 100%;
    width: 100%;
    background-color: white; /* Set a background color for the card */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Add a box shadow for a card-like appearance */

}

.header{
    position: 'absolute';
    top:'5px';
    background:'pink';
}

.content {
    display: inline;
    justify-content: end;
}


.bottom {
    align-items: flex-end;
}


.right-fix {
    /* height: 700px; */
    /* overflow-y: scroll; */
    /* // overflow-y: hidden; */
    overflow-x: hidden;

    height: 80vh !important;
    overflow-y: scroll !important;
    width: 80vw !important;
}

.my-notes-title {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    /* Your desired color */
}

.main_content_actionpoint .nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    border-right: none;


   .active {
        /* // background-color: blue !important; */
        background-color: #0d6efd !important;
        border-radius: 5px;
        a {
            color: white !important;
        }
    }
}
.nav-link{
    color: #f4f8ff;
}

.orange-bg {
    /* background-image: url(../Images/bgwall.jpg); */
    background: radial-gradient(#2a9bff, #262323);
    /* background-color: #0d6efd; */
    background-size: cover;
}

.lightgray-bg {
    /* background-image: url(../Images/notesbg.jpg); */
    background: radial-gradient(#2a9bff, #262323);
    /* background: radial-gradient(black, #ffd3d3); */
    background-size: cover;
}



#notes li {
    border-bottom: 1px solid #ccc;
    padding: 5px 0;
}

#notes li:last-child {
    border-bottom: none;
}


