.right-fix {
    height: 800px;
    overflow-y: scroll;
     /* overflow-y: hidden; */
    overflow-x: hidden;
}

.my-notes-title {
    font-size: 24px;
    font-weight: bold;
     /* color: #333; */
    /* Your desired color */
}

.main_content_actionpoint .nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    border-right: none;


   .active {
         /* background-color: blue !important; */
        background-color: #0d6efd !important;
        border-radius: 5px;
        a {
            color: white !important;
        }
    }
}

#notes li {
    border-bottom: 1px solid #ccc;
    padding: 5px 0;
}

#notes li:last-child {
    border-bottom: none;
}


